import axios from 'axios'
import Router from '../router'
import Store from '../store'
import {clearCookie} from '../utils/cookie'
axios.defaults.withCredentials=true;

let host = window.apiObj.apiUrl
if (window.apiObj.apiUrl == false) host = location.protocol + '//' + location.host


const service = axios.create({
    baseURL: host,
    // timeout: 5 * 1000,
    transformRequest: [function (data) {
        // 
        let param = new window.FormData();
        for(var n in data){
            param.append(n, data[n]);
        }
        return param;
    }],
    headers:{'Content-Type':'multipart/form-data'}
})

service.interceptors.request.use(config => {
    console.debug(`[UI] ${config.url}  ${config.method} `,config.data)
    return config
},error => {
    return Promise.reject(error)
})

service.interceptors.response.use(response => {

    var jsondata = response.data;

    console.debug("[UI] ", jsondata)
    response.data = jsondata
    // 
    if (Store.state.connectStatus.show && Store.state.connectStatus.type == "networkError"){
        Store.commit('setStatus', {"level": 'success',"msg": '',"type": null,"show": true});
        if (Store.state.beforestatus != null) {
            Store.dispatch('setStatusAsync',Store.state.beforestatus);
            Store.commit('beforestatus',null)
        }else{
            Store.dispatch('setStatusAsync',{"level": 'success',"msg": '',"type": null,"show": false})
        }
    }
    if(response.data.code == 6000) {
        clearCookie()
        Router.push("/login")
    }
    return response
}, error => {
    // 
    console.debug('[UI] ',error.message)
    if (error && error.response) {
        console.debug(`[UI] ${error.response.status}`)
    } else {
        // 
        if (JSON.stringify(error).includes('timeout')) {
            // 
            // 
            if(Store.state.connectStatus.show && Store.state.connectStatus.type != "networkError"){
                Store.commit('beforestatus',Store.state.connectStatus);
            }
            Store.commit('setStatus', {"level": 'error',"msg": '',"type": 'networkError',"show": true});
            console.debug('[UI] ')
        }
    }
    return Promise.resolve(error.response)
})
export default service
