import {createRouter,createWebHashHistory} from 'vue-router'

import Home from '../views/Home.vue'
import Room from '../views/Room.vue'
import Setting from '../views/Setting.vue'
import Login from '../views/Login.vue'
import Flv from '../views/flv.vue'
import About from '../views/About.vue'
import Files from '../views/Files.vue'
import geetest from '../views/geetest.vue'
import blogin from '../views/blogin.vue'


import {isAuthenticated} from '../utils/authenticated'

// Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      login: true,
      show: true,
      title: ''
    }
  },
  {
    path: '/about',
    name: 'About',
    component: About,
    meta: {
      login: false,
      show: false,
      title: ''
    }
  },
  {
    path: '/room',
    name: 'Room',
    component: Room,
    meta: {
      login: true,
      show: true,
      title: ''
    }
  },
  {
    path: '/files',
    name: 'Files',
    component: Files,
    meta: {
      login: true,
      show: true,
      title: ''
    }
  },
  {
    path: '/setting',
    name: 'Setting',
    component: Setting,
    meta: {
      login: true,
      show: true,
      title: ''
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      login: false,
      show: false,
      title: ''
    }
  },
  {
    path: '/gt',
    name: 'gt',
    component: geetest,
    meta: {
      login: false,
      show: false,
      title: ''
    }
  },
  {
    path: '/flv',
    name: 'flv',
    component: Flv,
    meta: {
      login: true,
      show: true,
      title: 'FLV'
    }
  },
  {
    path: '/blogin',
    name: 'blogin',
    component: blogin,
    meta: {
      login: true,
      show: true,
      title: 'B'
    }
  },

]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
})


router.beforeEach(async (to, from, next) => {
  //  title 
  document.title = `NGWORKS - ${to.meta.title}`
  let Authenticated = isAuthenticated()
  //  
  if (to.path == '/login' && Authenticated) {
    next({ path: from.path })
  }
  // //   : Login   
  // if (to.name !== 'Login' && to.name !== 'geetest' && !Authenticated) next({path: '/login' })
  // else 
  next()
})


export default router